.terms-app {
  position: relative;
  width: 100vw;
  height: 100%;
  background-color: #000;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  z-index: 300;
}

.terms-container {
  position: relative;
  width: 80%;
  height: 100%;
  padding: 10% 1%;
}


/* TEXT */

.terms-text-title {
  font-family: "ComicR";
  font-size: 3.5vw;
  font-weight: bold;
  color: #DDD;
  margin: 0;
  padding: 0;
  margin-bottom: 2vw;
}

.terms-text-description {
  font-size: 1.2vw;
  color: #858383;
  padding-bottom: 1.5vw;
}




